@use '../abstracts/variables';
@use '../abstracts/mixins';

.projects-container {
    margin: 5rem auto;
    max-width: 1200px;
    @include flexbox;
    align-items: center;
    flex-direction: column;

    h2 {
        margin-bottom: 5rem;
    }

    .project {
        @include flexbox;
        transition: 0.3s ease-in-out;
        width: 100%;
        border-radius: 20px;
        box-shadow: $shadow;
        background-color: $elements;
        
        &:nth-child(2), &:nth-child(3) {
            margin-bottom: 10rem;
        }

        .info {
            flex: 1;
            padding: 1.5rem;
            margin: auto 0;

            h3 { 
                margin-bottom: 1.75rem;

                &:nth-child(2) {
                    margin-top: 1.75rem;
                }
            }
            div {   
                width: 100%;
                @include flexbox-c;

                button {
                    @include button;
                    @include flexbox-c-c;
                    margin: 2rem 1.2rem 0 1.2rem;
                }
            }
        }

        .image {
            flex: 1;
            height: 400px;
            margin: auto 0;
            @include grid-center;

            img {
                width: 100%;
            }
        }
    }
}